import { Component, h, State } from '@stencil/core';

/**
 * Styling
 *
 * Using provided custom CSS properties,
 * it is possible to style the breadcrumbs.
 *
 * :::note
 * The `--breadcrumbs-item-icon-color` affects all icons.
 * However, the `iconColor` specified on individual items
 * will override that.
 * :::
 */
@Component({
    tag: 'limel-example-breadcrumbs-styling',
    shadow: true,
    styleUrl: 'breadcrumbs-styling.scss',
})
export class BreadcrumbsExampleStyling {
    @State()
    private items = [
        {
            text: 'Home',
            icon: 'home',
            iconColor: 'rgb(var(--color-cyan-light))',
        },
        {
            text: 'Products',
            icon: 'shop',
        },
        {
            text: 'Phones',
            icon: 'google_mobile_copyrighted',
        },
        {
            text: 'Accessories',
        },
    ];

    public render() {
        return <limel-breadcrumbs items={this.items} />;
    }
}
